import React, { useState } from 'react';
import { ActivityIndicator, Text, View, Image, Button, TouchableWithoutFeedback, FlatList} from 'react-native';
import data from './assets/cards.json';
import SmallClassImage from './SmallClassImage';
import SoundButton from './SoundButton';
import cardsets from './assets/cardsets.json';
import { Audio } from 'expo-av';
import { NavigationContainer, useScrollToTop } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { NavigationEvents } from "react-navigation";

const FlatListItemSeparator = () => {
  return (
    <View
      style={{
        height: 1,
        width: "100%",
        opacity: 0.25,
        backgroundColor: "#000000",
        marginRight: 8
      }}
    />
  );
}

const Tab = createBottomTabNavigator();
const Stack = createStackNavigator();

const App = () => {
  return (
    <NavigationContainer>
      <Stack.Navigator>
        <Stack.Screen name="Main" component={TabScreen} />
      </Stack.Navigator>
    </NavigationContainer>
  )
}

const TabScreen = ({ navigation }) => {
  return (
    <Tab.Navigator>
      <Tab.Screen name="SetList" component={SetList}/>
      <Tab.Screen name="Cards" component={CardListTab} initialParams = {{cardSet: null}}  />
    </Tab.Navigator>
  )
}

const SetList = ({ navigation }) => {
  const cardSets = cardsets.filter(x => x.craftable === 1)

  return (
    <View style={{ flex: 1, padding: 8 }}>
      <FlatList
        data={cardSets}
        renderItem={({ item }) => (
          <TouchableWithoutFeedback onPress= {e => {
              navigation.navigate('Cards', {
                cardSet: item.set
              })
            }
          }>
          <View style={{ flex: 1, flexDirection: 'row', alignItems: 'center'
          , justifyContent: 'space-between'
          , marginTop: 4, marginBottom: 4, marginRight: 8}}>
            <Text style={{flexGrow: 1}}>{item.name}</Text>
          </View>
          </TouchableWithoutFeedback>
        )}
        ItemSeparatorComponent={FlatListItemSeparator}
      />
    </View>
  );
}

const CardListTab = ( { route, navigation }) => {
  const flatListRef = React.useRef()
  useScrollToTop(flatListRef)
  const {cardSet} = route.params;
  const mydata = data
    .filter(x => (x.collectible === true && x.type === 'MINION' && (x.set === cardSet ||  cardSet === null)))
    .slice(0,100)

  navigation.addListener('tabPress', e => {
    navigation.navigate('Cards', {
        cardSet: null
    })
  })

  navigation.addListener('focus', e => {
    console.log('Test focus listener.')
    goToTop()
  })

  function goToTop() {
    console.log('GoToTop')
    flatListRef.current.scrollToOffset({ animated: true, offset: 0 })
  }

  return (

        <View style={{ flex: 1, padding: 8 }}>
          <FlatList
            data={mydata}
            renderItem={({ item }) => (
              <View style={{ flex: 1, flexDirection: 'row', alignItems: 'center'
              , justifyContent: 'space-between'
              , marginTop: 4, marginBottom: 4, marginRight: 8}}>
                <SmallClassImage cardClass={item.cardClass}/>
                <Text style={{flexGrow: 1}}>{item.name}</Text>
                <SoundButton title='Play' cardID={item.id} sound='P' />
                <View style={{padding: 4}} />
                <SoundButton title='Attack' cardID={item.id} sound='A' />
                <View style={{padding: 4}} />
                <SoundButton title='Death' cardID={item.id} sound='D' />
              </View>
            )}
            ItemSeparatorComponent={FlatListItemSeparator}
            ref={flatListRef}

          />
        </View>)
}

export default App;
